<template>
    <v-navigation-drawer 
        v-if="$vuetify.display.mobile"
        id="app-drawer"
        width="250"
        v-model="drawer" 
        :rail="rail"
        dark 
        app 
        order="1"
        elevation="0"
    >
        <v-list-item height="70" class="d-flex justify-center align-center">
            <v-list-item-title 
                    v-if="!rail"
                    class="text-h7 d-flex align-center break-word"
                >
                <span>
                    프로젝트 이름
                </span>
            </v-list-item-title>
        </v-list-item>
        <DefaultList :items="computedItems"/>
    </v-navigation-drawer>

    <v-app-bar 
        color=primary
        app 
        dark
        elevation="0"
    >
        <v-app-bar-nav-icon 
            v-if="$vuetify.display.mobile"
            @click="appBarNav"
        />
            <v-row class="flex-grow-1" align="center" justify="center" no-gutters>
                <v-avatar class="my-3 ml-2" size="40">
                    <v-img src="../assets/profile.jpg" alt="Profile" />
                </v-avatar>
                <span class="ml-3 font-weight-bold">
                    개인 포토 폴리오
                </span>
            </v-row>
    </v-app-bar>
    <v-app-bar v-if="!$vuetify.display.mobile" 
        color="white" elevation="0" border="b" order="2" height="70" density="compact">
      <!-- 탭 형태로 메뉴 나열 -->
      <v-container class="px-4">
        <v-tabs v-model="current" density="compact" grow>
          <v-tab
            v-for="(it, i) in computedItems"
            :key="i"
            :to="it.to"
            replace
          >
            <div v-if="it.title === 'HOME'">
                <v-icon :icon="it.icon"></v-icon>
            </div>
            <div v-else>
                <v-icon :icon="it.icon"></v-icon>
                {{ it.title }}
            </div>
          </v-tab>
        </v-tabs>
      </v-container>
    </v-app-bar>
    <v-main class="app-bk-color">
        <v-container>
            <v-spacer />
            <router-view />
        </v-container>
    </v-main>
</template>
<script>
    import DefaultList from '../layouts/menu/List.vue'
    export default {
        name: 'DefaultLayout',
        data(){
            return {
                drawer: true,
                rail: false,
                userItem: [  
                    { title: '대쉬보드', icon: 'mdi-view-dashboard', to: '/dashboard' },
                    { title: '대쉬보드', icon: 'mdi-arrange-bring-forward', to: '/stdd' },
                    { title: '대쉬보드', icon: 'mdi-alert-box', to: '/warningLog' },
                ],
                dialog: false,
            }
        },
        computed: {
            computedItems() {
                let items = [
                    { title: 'HOME', icon: 'mdi-home', to: '/dashboard', menuTitle: '메인 화면', divider: true, rail: this.rail },
                    { title: '당진교육문화스포츠센터', icon: 'mdi-view-dashboard', to: '/dangjin-fmresman', menuTitle: '대시 보드', divider: true, rail: this.rail },
                    { title: 'EA시스템', icon: 'mdi-arrange-bring-forward', to: '/stdd', menuTitle: '지적 기준점 관리', divider: true, rail: this.rail },
                    { title: '국가기록원', icon: 'mdi-map', to: '/toji', rail: this.rail },
                    { title: '충북 돌봄 시스템', icon: 'mdi-poll', to: '/poll', menuTitle: '설문조사', divider: true, rail: this.rail },
                    { title: '충북 출산휴가 시스템', icon: 'mdi-chart-box-outline', to: '/poll/status', rail: this.rail },
                    { title: '당진 토지관리 통합 플렛폼', icon: 'mdi-account', to: '/users', menuTitle: '사용자 관리', divider: true, rail: this.rail },
                    { title: '정선 업무관리 플렛폼', icon: 'mdi-alert-box', to: '/warningLog', divider: true, rail: this.rail },
                ];
                return items;
            }
        },
        methods: {
            logout() {
                this.dialog = true;
            },
            appBarNav() {
                if(!this.$vuetify.display.mobile) {
                    this.rail = !this.rail;
                }else{
                    this.drawer = !this.drawer;
                }
            }
        },
        created() {

        },
        components: {
            DefaultList,
        }
    }
</script>
<style>
.v-navigation-drawer {
    border-right: none !important;
    box-shadow: none !important;
}
.break-word {
    white-space: normal !important;
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
    text-align: center;
}
</style>